.projects {
    &--no-cursor {
        cursor: none;
    }
    &__item {
        position: relative;
        overflow: hidden;
        &-img {
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            transform: scale(1.5);
            transition: 1000ms ease;
            transform-origin: 50% 100%;
            object-position: center;
            object-fit: cover;
        }

        &--active {
            img {
                transform: scale(1);
            }
        }

        &--hide {
            img {
                transform: scale(1.5);
            }
        }

        &:first-child {
            img {
                opacity: 1;
                transform: scale(1);
            }
        }

        &-overlay {
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 2;
        }
    }
    .fp-watermark {
        display: none !important;
    }

    &__info {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 4;
        color: #fff;

        &-container {
            display: flex;
            align-items: flex-end;
            height: 100%;
            width: 100%;
            justify-content: flex-end;
            overflow: hidden;
            position: relative;
        }

        &-right {
            display: flex;
            align-items: flex-end;
            position: relative;
            transition: 800ms ease all;
            transform: translateX(calc(436px));
        }
    }

    &__partners {
        transform: translateX(60px);
        transition: 800ms ease all;
        margin-bottom: 50px;
        height: 140px;
        overflow: hidden;

        &-wrapper {
            position: relative;
            transition: 800ms ease all;
            top: 0;
        }

        &-slide {
            height: 140px;
        }
        &-title {
            font-size: 16px;
            text-align: right;
            margin-bottom: 50px;

            &--lg {
                font-size: 24px;
                font-weight: 800;
            }
        }

        &-container {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            gap: 32px;
        }

        &-link {
            font-size: inherit;
            line-height: inherit;
            display: inline-flex;
        }
    }

    &__menu-container {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        height: calc(100vh - 112px);
        max-width: 496px;
    }

    &__menu-btns {
        display: flex;
        position: relative;
        z-index: 6;
        top: 150px;
    }

    &__menu-close {
        .projects__menu-icon {
            transform: rotate(180deg);
        }
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        opacity: 0;
    }

    &__menu-icon {
        circle,
        path {
            transition: 300ms ease all;
        }
    }

    &__menu-open {
        &:hover {
            .projects__menu-icon {
                circle {
                    fill: #fff;
                }
                path {
                    fill: #000;
                }
            }
        }
    }

    &__menu-close {
        transition: 500ms ease all;
        &:hover {
            .projects__menu-icon {
                circle {
                    fill: #fff;
                }
                path {
                    fill: #000;
                }
            }
        }
    }

    &__menu-group {
        display: flex;
        align-items: flex-start;
    }

    &__menu-links {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        gap: 8px;
        transform: translateX(120px);
    }

    &__menu-link {
        border-radius: 24px;
        background: rgba(255, 255, 255, 0.1);
        font-size: 16px;
        font-size: 16px;
        color: #fff;
        padding: 4px 8px;
        border: 1px solid transparent;
        transition: 300ms ease all;
        &:hover {
            border-color: #fff;
        }

        &--active {
            background: #fff;
            color: #242b2f;
        }
    }

    &__menu {
        transform: translateX(60px);
        height: 500px;
        overflow: hidden;

        &-wrapper {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 8px;
            position: relative;
        }

        &-item {
            font-size: 24px;
            line-height: 110%;
            transition: 400ms ease all;
            opacity: 0.2;
            display: inline-flex;
            span {
                display: inline-block;
                margin-right: 24px;
                min-width: 40px;
            }

            &--active {
                opacity: 1;
                font-size: 40px;
            }
        }
    }

    &__info-right--active {
        transform: translateX(0);

        .projects__menu-open {
            opacity: 0;
            z-index: -1;
        }
        .projects__menu-close {
            opacity: 1;
            z-index: 4;
            transform: translateX(-54px);
        }

        .projects__menu-links {
            transform: translateX(60px);
            width: calc(100% - 60px);
        }

        .projects__menu,
        .projects__partners {
            transform: translateX(0);
        }
    }

    &__button {
        width: 140px;
        height: 140px;
        background-color: #fff;
        border-radius: 50%;
        background-image: url(../img/projects/cursor.gif);
        background-position: center;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px;
        font-weight: 700;
        position: absolute;
    }

    &__info-left {
        display: flex;
        align-items: flex-end;
        flex-direction: column;
        margin-bottom: 50px;
        position: absolute;
        left: 60px;
        bottom: 0;
    }

    &__main-info {
        overflow: hidden;
        max-height: 280px;

        &-wrapper {
            position: relative;
            transition: 800ms ease all;
            top: 0;
        }
        &-slide {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            height: 280px;
        }
        &-desc {
            font-size: 24px;
            font-weight: 600;
            margin-bottom: 15px;
        }
        &-title {
            font-size: 96px;
            font-weight: 600;
            line-height: 160%;
            margin-bottom: 18px;
        }
        &-tags {
            display: flex;
            align-items: flex-start;
            gap: 12px;
            span {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                border-radius: 24px;
                border: 1px solid #fff;
                padding: 0 12px;
                font-size: 16px;
            }
        }
    }
}
